.base {
  border-style: solid;
  border-width: 2px;
  border-radius: 15px;
  border-color: rgb(64, 64, 65);
  padding: 10px;
  margin: 10px;
  background-color: #d9d4d0;

  display: grid;
  grid-template-columns: repeat(3, 1fr); /*3 columnas de 1 fraccion*/
  grid-template-rows: 1fr 2fr; /*la 2da fila del doble q la primera*/
  position: relative;
  width: 25rem;
  /* display: flex;
  flex-direction: column;
  align-self: flex-end;
  width: 100%; */
}

.divinterno {
  /* display: flex;
  flex-direction: row; */
}

.ciudad {
  grid-column: 1 / span 3;
  align-self: center;
  color: rgb(151, 16, 163);
  font-weight: bold;
  font-size: 2rem;
}

.temp {
  padding: 10px;
  margin: 10px;
  border-style: solid;
  border-radius: 5px;
  border-color: rgb(68, 68, 71);
}

.maxmin {
  color: rgb(76, 16, 150);
  font-size: 1.2rem;
}

.nrotemp {
  color: rgb(8, 97, 102);
  font-size: 1rem;
}

.boton {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  font-size: 1rem;

  cursor: pointer;
  background-color: red;
  color: white;

  padding-left: 0.55rem;
  padding-right: 0.55rem;
  padding-bottom: 0.1rem;

  display: flex;
  justify-content: center;
  align-items: center;

  border-style: solid;
  border-radius: 2px;
  border-color: transparent;
  border-radius: 9999px; /* aseguro que quede redondo */
}

.boton:hover {
  background-color: rgb(255, 158, 1);
  color: rgb(0, 0, 0);
}

.icono {
  align-self: center;
  /* width: 60%; */
}
